<template>
  <div class="table">
    <div class="table-header">
      <b class="table-title">{{ title }}</b>
      <el-button v-if="team.name" type="primary" size="medium" @click="createTeam">
        修改信息
      </el-button>
      <el-button v-else type="primary" size="medium" @click="createTeam">
        {{ operation }}
      </el-button>
    </div>

    <el-descriptions v-if="team.name" :column="3" border>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-user"></i>
          队伍名
        </template>
        {{ team.name }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-mobile-phone"></i> 队伍简称
        </template>
        {{ team.nick }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-location-outline"></i>
          居住地
        </template>
        苏州市
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-tickets"></i>
          备注
        </template>
        {{ team.remark }}
      </el-descriptions-item>
      <el-descriptions-item>
        <template slot="label">
          <i class="el-icon-office-building"></i>
          联系地址
        </template>
        江苏省苏州市吴中区吴中大道 1188 号
      </el-descriptions-item>
    </el-descriptions>

    <el-empty
      v-else
      description="暂无，点击右上角添加吧"
      :image-size="80"
    ></el-empty>

    <el-dialog :title="title" :visible.sync="dialogVisible">
      <el-form
        label-position="left"
        label-width="80px"
        :model="Form"
        :rules="formRules"
        ref="Form"
      >
        <el-form-item label="队伍全称" prop="name">
          <el-input v-model="Form.name"></el-input>
        </el-form-item>
        <el-form-item label="队伍简称" prop="nick">
          <el-input v-model="Form.nick"></el-input>
        </el-form-item>
        <el-form-item label="备注">
          <el-input type="textarea" v-model="Form.remark"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onFormSubmit">确定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "TeamDescription",
  props: {
    title: String,
    operation: String,
  },
  data() {
    return {
      team: {},
      dialogVisible: false,
      Form: {},
      formRules: {
        name: [{ required: true, trigger: "blur", message: "用户名不能为空" }],
        nick: [{ required: true, trigger: "blur", message: "手机号不能为空" }],
      },
    };
  },
  created() {},
  methods: {
    onFormSubmit() {
      this.$refs.Form.validate((valid) => {
        if (valid) {
          const { name, nick, remark } = this.Form;
          this.team = { name, nick, remark };
          this.dialogVisible = false;
        }
      });
    },
    createTeam() {
      const { name, nick, remark } = this.team;
      this.Form = { name, nick, remark };
      this.dialogVisible = true;
    },
  },
};
</script>


<style scoped>
.table-header {
  margin: 20px 0;
  line-height: 36px;
}

.table-header > .el-button {
  float: right;
}

.table-title {
  font-size: 20px;
}

.el-empty {
  background-color: #FAFAFA;
}
</style>
