<template>
  <div class="table">
    <div class="table-header">
      <b class="table-title">{{ title }}</b>
      <el-button type="primary" size="medium" @click="addNew">
        {{ operation }}
      </el-button>
    </div>
    <el-table v-if="athletes.length" :data="athletes" style="width: 100%">
      <el-table-column prop="name" label="姓名" width="180"> </el-table-column>
      <el-table-column prop="phone" label="联系电话" width="180">
      </el-table-column>
      <el-table-column prop="remark" label="备注"> </el-table-column>
      <el-table-column align="right">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index)">
            修改
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-empty
      v-else
      description="暂无，点击右上角添加吧"
      :image-size="80"
    ></el-empty>

    <el-dialog :title="title" :visible.sync="dialogVisible">
      <el-form
        label-position="left"
        label-width="80px"
        :model="Form"
        :rules="formRules"
        ref="Form"
      >
        <el-form-item label="姓名" prop="name">
          <el-input v-model="Form.name"></el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="phone">
          <el-input v-model="Form.phone"></el-input>
        </el-form-item>
        <el-form-item label="备注">
          <el-input type="textarea" v-model="Form.remark"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onFormSubmit">确定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "AthletesTable",
  components: {},
  props: {
    title: String,
    operation: String,
  },
  data() {
    return {
      athletes: [],
      dialogVisible: false,
      Form: {},
      formRules: {
        name: [{ required: true, trigger: "blur", message: "用户名不能为空" }],
        phone: [{ required: true, trigger: "blur", message: "手机号不能为空" }],
      },
      modifyIndex: -1,
    };
  },
  created() {},
  methods: {
    addNew() {
      this.modifyIndex = -1;
      this.Form = {};
      this.dialogVisible = true;
    },
    handleEdit(index) {
      this.modifyIndex = index;

      const { name, phone, remark } = this.athletes[index];
      this.Form = { name, phone, remark };

      this.dialogVisible = true;
    },
    onFormSubmit() {
      this.$refs.Form.validate((valid) => {
        if (valid) {
          const { name, phone, remark } = this.Form;
          if (this.modifyIndex === -1) {
            // 新增
            this.athletes.push({ name, phone, remark });
          }
          // 修改
          else
            this.$set(this.athletes, this.modifyIndex, { name, phone, remark });

          this.dialogVisible = false;
        }
      });
    },
    handleDelete(index) {
      this.athletes.splice(index, 1);
    },
  },
};
</script>


<style scoped>
.table-header {
  margin: 20px 0;
  line-height: 36px;
}

.table-header > .el-button {
  float: right;
}

.table-title {
  font-size: 20px;
}

.el-empty {
  background-color: #FAFAFA;
}
</style>
